import React from 'react';
import ReactDOMServer from 'react-dom/server';
import {StaticRouter, matchPath} from 'react-router';
import {Provider} from 'react-redux';
import configure from './store/configure';
import routes from './routes';
import axios from 'axios';

import App from './compoenets/App';
import {Helmet} from 'react-helmet';

const render = async (ctx) => {
    const {url, origin} = ctx;          // URL .
    //render   ctx  Koa  ctx  .
    //  Origin  baseURL  
    axios.defaults.baseURL = origin;
    const context = {};
    //       .
    const store = configure();

    const promises = [];
    //       .
    routes.forEach(route =>{
        const match = matchPath(url, route);
        //   .
        if(!match) return;

        // match      preload .
        //   params preload  .
        const { component } = route;
        const { preload } = component;
        if(!preload) return;
        // preload , store.dispatch params  preload promise   
        // promises . 

        const {params} = match;
        // Route props  match  .
        // preload    promise  .

        const promise = preload(store.dispatch, params);
        promises.push(promise);
    }
    );

    try{
        //    .
        //  preload     ,      .
        await Promise.all(promises);
    }
    catch(e){

    }


    // ReactDOMSAerver   .       .
    // renderToString     .
    //  BrowserRouter  StaticRouter .
    const html = ReactDOMServer.renderToString(
        <Provider store={store}>
            {/*
            BrowserRouter    HTML5 History API     
            StaticRoruter    url props     .
            */}
            <StaticRouter location={url} context={context}>
                <App/>
            </StaticRouter>
        </Provider>
    );
    if(context.isNotFound){
        ctx.status = 404;
        // HTTP  404 
    }
    //    renderStatic()   .
    Helmet.canUseDOM = false;
    const helmet = Helmet.renderStatic();
    // helet   renderStatic  
    // renderStatic       .

    return {html, helmet};
}

export default render;